import React from "react";
import PropTypes from "prop-types"; //Se usa para definir los tipos de datos
import "./Card.css";

const Card = ({lang, img, fcolor, scolor}) => { //Las variables se desestructuran usando {}
    return (
        <div className="card" style={{
            background: `linear-gradient(to left, ${fcolor}, ${scolor})`,
        }}>
            <img src={ img } alt="logo.svg" />
            <h3>{lang}</h3>
        </div>
    )
}

Card.propTypes = {
    lang: PropTypes.string,
    img: PropTypes.string,
    fcolor: PropTypes.string,
    scolor: PropTypes.string,
}


export default Card